import React, { useState, useEffect, useLayoutEffect } from 'react'
import useForm from 'react-hook-form'
import styles from './index.module.scss'

export default function Element(props) {
    const { register, handleSubmit, errors,setValue } = useForm({
        validateCriteriaMode: "all",
        mode: "onChange"
    });
    useLayoutEffect(() => {
        setValue('username','waaaaaaaaa')
    },[])
    const onSubmit = data => {
        console.log(data)
    };
    return (
        <div className={styles.form}>
            <form onSubmit={handleSubmit(onSubmit)}>
                <div>
                    <label htmlFor="username">User Name</label>
                    <input type='username' name="username" placeholder="Bill" ref={register({ required: true, minLength: 10, maxLength: 16 })} />
                    {errors.username && errors.username.types.required && (
                        <p>username required</p>
                    )}
                    {errors.username && errors.username.types.minLength && (
                        <p>username minLength 10</p>
                    )}
                    {errors.username && errors.username.types.maxLength && (
                        <p>username maxLength 16</p>
                    )}
                </div>

                <div>
                    <label htmlFor="lastName">Last Name</label>
                    <input type='lastName' name="lastName" placeholder="Luo" ref={register({ required: true, minLength: 10, maxLength: 16 })} />
                    {errors.lastName && errors.lastName.types.required && (
                        <p>lastName required</p>
                    )}
                    {errors.lastName && errors.lastName.types.minLength && (
                        <p>lastName minLength 10</p>
                    )}
                    {errors.lastName && errors.lastName.types.maxLength && (
                        <p>lastName maxLength 16</p>
                    )}
                </div>

                <div>
                    <label type='email' name='email' htmlFor="email">Email</label>
                    <input
                        name="email"
                        placeholder="bluebill1049@hotmail.com"
                        type="text"
                        ref={register({ required: true, minLength: 10, pattern: /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/ })}
                    />
                    {errors.email && errors.email.types.required && (
                        <p>email required</p>
                    )}
                    {errors.email && errors.email.types.minLength && (
                        <p>email minLength 10</p>
                    )}
                    {errors.email && errors.email.types.pattern && (
                        <p>邮箱格式错误</p>
                    )}
                </div>

                <div style={{ color: "red" }}>
                    {Object.keys(errors).length > 0 &&
                        "There are errors, check your console."}
                </div>
                <input type="submit" />
            </form>
        </div>
    )
}